import UserCard from "components/section-components/UserCard/UserCard";
import React from "react";
import { withRouter } from "react-router";
import { NavLink } from "react-router-dom";
import { Col } from "reactstrap";
import { DASHBOARD, ENQUIRIES, FAVOURITES, SAVE_SEARCHES } from "./Constants";

const VerticalMenu = ({ match: { path }, ...props }) => {
  const {
    location: { pathname },
  } = props;
  const activeclass = {
    color: "rgb(255, 255, 255)",
    transition: "all 0.5s ease",
    backgroundColor: "#40586d",
  };

  return (
    <Col md="3">
      <div className="userDetailsMenu userDetailsMenuDesktop">
        <ul className="vertical-menu__list">
          <UserCard />
          <li>
            <NavLink
              activeStyle={pathname === DASHBOARD && activeclass}
              to={path}
            >
              Dashboard
            </NavLink>
          </li>
          <li>
            <NavLink
              activeStyle={activeclass}
              to={`${path}${FAVOURITES}`}
              target=""
            >
              Favourites
            </NavLink>
          </li>
          <li>
            <NavLink
              activeStyle={activeclass}
              to={`${path}${SAVE_SEARCHES}`}
              target=""
            >
              Saved Searches
            </NavLink>
          </li>
          <li>
            <NavLink
              activeStyle={activeclass}
              to={`${path}${ENQUIRIES}`}
              target=""
            >
              Your Enquiries
            </NavLink>
          </li>
        </ul>
      </div>
    </Col>
  );
};

export default withRouter(VerticalMenu);
