import React from 'react';
import { connect } from "react-redux";
import { Route, Switch, withRouter } from "react-router-dom";
import { Container, Row } from "reactstrap";
import DashboardMainMenu from "../../components/DashboardMainMenu";
import BaseIndexPage from "../../components/HOC/BaseIndexPage";
import { ENQUIRIES, FAVOURITES, SAVE_SEARCHES } from "./Components/Constants";
import Dashboard from "./Components/Dashboard";
import Enquiries from "./Components/Enquiries";
import Favourites from "./Components/Favourites";
import SaveSearches from "./Components/SaveSearches";
import VerticalMenu from "./Components/VerticalMenu";

const Index = ({ match: { path }, ...props }) => {
  return (
    <div className=" mg-top-90 ">
      <DashboardMainMenu />
      <Container>
        <Row className="py-4">
          <VerticalMenu />
          <Switch>
            <Route exact path={path}>
              <Dashboard />
            </Route>
            <Route exact path={`${path}${FAVOURITES}`}>
              <Favourites />
            </Route>
            <Route exact path={`${path}${SAVE_SEARCHES}`}>
              <SaveSearches />
            </Route>
            <Route exact path={`${path}${ENQUIRIES}`}>
              <Enquiries />
            </Route>
          </Switch>
        </Row>
      </Container>
    </div>
  );
};

const mapStateToProps = (state) => {
  return {
    noHeader: true,
    noBanner: true,
  };
};

export default connect(mapStateToProps)(withRouter(BaseIndexPage(Index)));
